export default {
	name: 'basics',
	data() {
		return {
			tarHeight: 50,
			isRight: true,
			screenHeight: 0,
			mapHeight: 500,
			contentheght: 110,
			title: 'map',
			latitude: 39.909,
			longitude: 116.39742,
			covers: [
				{
					latitude: 39.909,
					longitude: 116.39742,
					iconPath: '../../static/location.png',
					callout: {
						content: '无法服务，附近暂无技师',
						bgColor: '#d81e06',
						borderColor: '#d81e06',
						borderRadius: 50,
						display: 'ALWAYS',
						padding: 10,
						fontSize: 15,
						color: '#fff'
					}
				}
			]
		};
	},
	onShow() {
		console.log('success');
	},
	methods: {
		//选择车辆地址
		doSelectLocations() {
			uni.chooseLocation({
				success: function(res) {
					console.log('位置名称：' + res.name);
					console.log('详细地址：' + res.address);
					console.log('纬度：' + res.latitude);
					console.log('经度：' + res.longitude);
				}
			});
		},
		//选择车辆
		doSelectCars() {
			this.isRight = !this.isRight;
			if (this.isRight) {
				this.contentheght = 110;
			} else {
				this.contentheght = 220;
			}
			this.mapHeight = this.screenHeight - this.contentheght;
		}
	},
	mounted() {
		this.screenHeight = uni.getStorageSync('screenHeight') - 50;
		this.mapHeight = this.screenHeight - this.contentheght;
	}
};
